/* reset default focusing */
input, button, [class|="btn"], [class*=" btn-"],
.social-networks__item a, .f-soc__item a,
.pop__open, .rolls__angle, .rolls__header, .big-btn,
.clients__item__inner {
	&:focus {
		outline: none; } }

/* reset default focusing end */

/* social networks */
