	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.social-networks__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -10px -20px;

	@include media('<tab-lg') {
		margin: 0 -5px -10px; } }

