.social-networks__item {
	float: left;
	margin: 0 10px 20px;

	@include media('<tab-lg') {
		margin: 0 5px 10px; } }

.social-networks__item, .f-soc__item {
	a {
		display: block;
		position: relative;
		font-size: 60px;
		@include size(1em);

		@include media('<tab-lg') {
			font-size: 40px; }

		&::before, &::after {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			@include size(100%);
			background-image: url("../../img/soc-sprite.svg");
			background-repeat: no-repeat;
			background-size: 12em auto; // размер файла спрайта
			// transition
			//$prop: opacity
			//will-change: $prop
			//transition:
			//roperty: $prop
			//uration: $animation-speed * 2
 }			//iming-function: ease-in-out

		&::after {
			opacity: 0; }

		// hover
		&:hover {
			&::before {
				opacity: 0; }

			&::after {
				opacity: 1; } }

		$soc-list: 'facebook', 'telegram', 'viber';
		$type-length: 4; // количество цветов одной иконки в спрайте

		@for $i from 1 through length($soc-list) {
			$pos: -$type-length * $i * 1em + $type-length;

			&.#{nth($soc-list, $i)} {
				&::before {
					$p: $pos;
					background-position: $p $p; }

				&::after {
					$p: $pos - 1;
					background-position: $p $p; }

				.user-form__msg__soc & {
					&::before {
						$p: $pos - 2;
						background-position: $p $p; }

					&::after {
						$p: $pos - 3;
						background-position: $p $p; } } } }

		span {
			@include hide-text; } } }

/* social networks end */

/* social networks in footer */
.f-soc {
	overflow: hidden;
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; }

	@include media('<mob-lg') {
		display: flex;
		justify-content: center;
 }		//padding-bottom: 3rem

	&:last-child {
		margin-bottom: 0; } }

.f-soc__group {
	float: left;
	margin-left: nth($footer-box-space, 2);

	@include media('<desk-lg') {
		margin-left: nth($footer-box-space--md, 2); }

	@include media('<tab-lg') {
		margin-left: nth($footer-box-space--mob, 2); }

	&:first-child {
		margin-left: 0; }

	& > strong {
		display: block;
		margin-bottom: 5px;
		@include font-light;
		font-size: 14px;
		line-height: 18px;
		color: $text-color;
		opacity: 0.4;

		@include media('<tab-lg') {
			font-size: 12px; }

		@include media('<mob-lg') {
			text-align: center; } } }

.f-soc__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -15px -15px 0;

	@include media('<tab-lg') {
		margin: 0 -10px -10px 0; } }

.f-soc__item {
	float: left;
	margin: 0 15px 15px 0;

