/* user form */
.user-form {
	position: relative;
	margin-bottom: $bottom-space--section + 0.4; //костыль для подгонки отступов под дизайн
	background-color: $base-color-alt;
	border-radius: round($form-input-height/2);
	padding: 6.9rem nth($page-layout-space, 2) 8rem;
	@include wide;

	@include media('<desk-lg') {
		padding: 6.9rem nth($page-layout-space--md, 2) 8rem; }

	@include media('<tab-lg') {
		padding: 0;
		border-radius: 0;
		background: none; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space--section--mob; }

	.layout & {
		@include media('<tab-lg') {
			@include margin-horizontal(-#{nth($page-layout-space--mob, 2)}); } }

	&:last-child {
		margin-bottom: 0; }

	.form-title {
		display: block;
		text-align: center;
		margin-bottom: $bottom-space--title;
		color: $highlight-color;

		@include media('<tab-lg') {
			margin-bottom: 3.1rem;
			@include padding-horizontal(#{nth($page-layout-space--mob, 2)}); }

		@include media('<mob-lg') {
			margin-bottom: 0.7rem; }
		// font size
		& {
			@include font-h3; } }

	.form-title, p {
		max-width: $text-layout-width;
		@include push-auto;

		@include media('<tab-lg') {
			max-width: none; }

		&:last-child {
			margin-bottom: 0; } }

	form {
		position: static;
		display: block;
		margin-bottom: 6rem;

		@include media('<tab-lg') {
			margin-bottom: 0;
			padding: 4rem nth($page-layout-space--mob, 2) 3rem;
			background-color: $base-color-alt; }

		@include media('<mob-lg') {
			padding: 2.5rem nth($page-layout-space--mob, 2) 1.5rem; }

		&:last-child {
			margin-bottom: 0; } }

	.form-buttons {
		@include media('<tab-lg') {
			margin-bottom: 0;
			padding-top: 2rem; }

		@include media('<mob-lg') {
			padding-top: 0.5rem; }

		label, .label-holder {
			@include media('<tab-lg') {
				display: none; } } }

	button {
		display: block;
		width: 100%;
		margin-left: auto;

		@include media('<tab-lg') {
			max-width: 328px; }

		@include media('<mob-lg') {
			max-width: none;
			padding-left: 21px;
			padding-right: 40px; }

		svg {
			@include media('<mob-lg') {
				padding-right: 22px; } } }

	.social-networks {
		@include media('<tab-lg') {
			display: none; } } }

.user-form__note {
	margin-bottom: 4.3rem;

	@include media('<tab-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2));
		margin-bottom: 4rem; }

	@include media('<mob-lg') {
		margin-bottom: 2.3rem; } }

.user-form__msg {
	position: absolute;
	right: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	display: block;
	display: flex;
	flex-direction: column;
	justify-content: center;
	//justify-content: flex-start
	align-items: center;
	background-color: rgba($highlight-color, 0.95);
	padding: 3.9rem nth($page-layout-space, 2) 3rem;
	color: $base-color;
	opacity: 0;
	pointer-events: none;
	z-index: $z-middle;
	border-radius: round-float($form-input-height / 2, 0);
	// transition
	$prop: opacity;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	@include media('<desk-lg') {
		width: 100%;
		padding: 2rem nth($page-layout-space--md, 2) 1.5rem; }

	@include media('<tab-lg') {
		padding: $page-layout-space--mob;
		border-radius: 0; }

	.form-success & {
		opacity: 1;
		pointer-events: auto; } }

.user-form__msg__caption {
	text-align: center;
	margin-bottom: 4.6rem;

	@include media('<mob-lg') {
		margin-bottom: 2rem; }

	&:last-child {
		margin-bottom: 0; } }

.user-form__msg__title {
	color: white;

	& {
		@include font-h1;

		@include media('<mob-lg') {
			text-align: center; } } }

.user-form__msg__subtitle,
.user-form__msg__title {
	display: block;
	margin-bottom: 2.2rem;

	@include media('<tab-lg') {
		margin-bottom: 2.0rem; }

	&:last-child {
		margin-bottom: 0; } }

.user-form__msg__subtitle,
.user-form__msg__tel {
	display: block;
	max-width: 600px;
	@include push-auto; }

.user-form__msg__subtitle {
	@include font-h3($base-color);

	@include media('<mob-lg') {
		text-align: center; } }

.user-form__msg__tel {
	@include font-bold;
	@include font(3.6rem, 4.2rem);
	margin-bottom: 6.9rem;

	@include media('<tab-lg') {
		margin-bottom: 0; }

	@include media('<mob-lg') {
		@include font(2.6rem, 3.2rem); }

	&:last-child {
		margin-bottom: 0; }

	a {
		color: inherit;
		text-decoration: none;

		.no-touchevents &:hover {
			color: $base-color-dark; } } }

.user-form__msg__soc {
	@include media('<tab-lg') {
		display: none; } }

/* user form end */

/* slick sliders */
// slider
.slick-slide {
	-webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
	-webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
	outline: none !important; }

// arrows
.slick-arrow {
	@include size(40px);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(12px);
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;
	pointer-events: none;
	bottom: -2.8rem;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;
		pointer-events: auto;

		&, .#{$user-content} & {
			padding: 3px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 10px;
			height: 10px;
			padding: 0;
			border-radius: 50%;
			background: $text-color-light;
			position: relative;
			transition: background-color $animation-speed ease-in-out;

			&::before {
				content: none; } }

		&.slick-active button {
			background-color: $highlight-color; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* breadcrumbs */
.breadcrumbs {
	margin-top: -0.9rem; //костыль для подгонки отступов под дизайн
	margin-bottom: $bottom-space--section + 0.2; //костыль для подгонки отступов под дизайн
	@include wide;

	@include media('<mob-lg') {
		margin-top: -0.7rem;
		margin-bottom: 5.6rem; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0;
			margin: 0;
			@include font($font-size--small, $line-height--small);

			@include media('<mob-lg') {
				font-size: 12px;
				line-height: 14px; }

			&::before, &:first-child::before {
				content: none; } } }

	& {
		cursor: default;
		text-align: center;

		@include media('<mob-lg') {
			text-align: left; } }

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '/';
			border: none;
			color: $highlight-color;
			padding-left: 0.25em;

			@include media('<mob-lg') {
				padding-left: 0.22em; } }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: $highlight-color;
		text-decoration: none;
		cursor: pointer;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		//span
		//order-bottom: 1px solid transparent
		/// transition
		//prop: border-color
		//ransition:
		//property: $prop
		//duration: $animation-speed

		&:hover {
			color: $highlight-color--hover; } } }
//text-decoration: underline

//span
//order-color: $highlight-color

/* breadcrumbs end */

/* buy btn */
.buy {
	text-align: center;
	margin-bottom: 4rem;

	@include media('<tab-lg') {
		margin-bottom: 2rem;
		text-align: left; }

	.breadcrumbs + & {
		margin-top: -10rem;

		@include media('<tab-lg') {
			margin-top: -2rem; } } }

/* buy btn end */

/* pop */
.pop {
	display: inline-block;
	vertical-align: top;
	position: relative;
	color: inherit;
	cursor: default; }

.pop__open {
	display: inline-block;
	vertical-align: top;
	position: relative;
	padding: 0 nth($pop-opener-space, 2);
	@include font-medium;
	color: inherit;
	white-space: nowrap;
	cursor: pointer;
	// transition
	transition: color $animation-speed ease-in-out;

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: $pop-opener-left--outline; // костыль для подгонки отступов под дизайн
		top: -#{nth($pop-opener-space--outline, 1)};
		width: calc(100% - 7px); // костыль для подгонки отступов под дизайн
		height: calc(100% + #{nth($pop-opener-space--outline, 1) + nth($pop-opener-space--outline, 3)}); //костыль для подгонки отступов под дизайн
		border-radius: 20px;
		border: 2px solid $highlight-color;
		// transition
		$prop: transform, opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed;
			timing-function: ease-in-out; }

		@include media('<mob-lg') {
			left: 3px;
			top: -2px;
			width: calc(100% - 6px);
			height: calc(100% + 3px);
			border-radius: 12px; } }

	//.pop-open:active
	//ransform: translate(0, 1px)

	.pop-is-open & {
		z-index: 10;
		cursor: default;
		pointer-events: none;

		@include media('>=tab-lg') {
			&::before {
				opacity: 0; } } }

	// hover
	.no-touchevents &:hover {
		//color: $highlight-color--hover
		color: currentColor; } }

//.no-touchevents .pop-open:hover
//include media('>=tab-lg')
//color: $base-color

.pop__content {
	position: absolute;
	left: $pop-opener-left--outline; // костыль для подгонки отступов под дизайн
	top: -#{nth($pop-opener-space--outline, 1)};
	width: 440px;
	padding: 5.4rem nth($grid-space, 2) 3.2rem; // костыль для подгонки отступов под дизайн
	@include font-light;
	@include font(2.0rem, 3.0rem);
	color: $base-color;
	background-color: $highlight-color;
	border-radius: 15px;
	z-index: 5;
	opacity: 0;
	pointer-events: none;
	$translate: 0, 2em;
	transform: translate($translate);
	transform: translate3d($translate, 0);
	// transition
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $pop-animation-speed;
		timing-function: cubic-bezier(0.55, 0.085, 0, 0.99); }

	.position-right & {
		left: auto;
		right: 0; }

	@include media('<tab-lg') {
		position: fixed;
		left: 50%;
		top: 50%;
		padding: 4rem nth($grid-space--mob, 2) 2.5rem;
		max-width: calc(100% - #{nth($page-layout-space--mob, 2) * 2});
		box-shadow: 0 0 30px rgba(black, 0.2);
		z-index: 15;
		$translate: -50%, calc(-50% + 2em + #{round($header-height--mob / 2)});
		transform: translate($translate);
		transform: translate3d($translate, 0); }

	@include media('<mob-lg') {
		@include font($font-size--mob, $line-height--mob); }

	.pop-is-open & {
		opacity: 1;
		pointer-events: auto;
		$translate: 0, 0;
		transform: translate($translate);
		transform: translate3d($translate, 0);

		@include media('<tab-lg') {
			$translate: -50%, calc(-50% + #{round($header-height--mob / 2)});
			transform: translate($translate);
			transform: translate3d($translate, 0); } } }

.pop__title {
	@include font-bold;
	@include font(2.2rem, 3.0rem);
	display: block;
	margin-bottom: 0.7em;

	@include media('<mob-lg') {
		@include font(1.8rem, 2.4rem); } }

.pop__close {
	$line-w: 20px;
	$line-h: 2px;
	$pos: 8px;
	position: absolute;
	top: $pos;
	right: $pos;
	width: $line-w;
	height: $line-w;
	color: white;
	text-indent: -9999px;
	cursor: pointer;
	overflow: hidden;
	opacity: 0;
	$rotate: -0.25turn;
	transform: rotate($rotate);
	transform: rotate($rotate) translateZ(0);
	// transition
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		//delay: round-float($pop-animation-speed / 2, 1)
		delay: 0s;
		property: $prop;
		duration: $pop-animation-speed;
		timing-function: ease-in-out; }

	.position-right & {
		right: auto;
		left: $pos; }

	.pop-is-open & {
		opacity: 1;
		$rotate: 0turn;
		transform: rotate($rotate);
		transform: rotate($rotate) translateZ(0);
		transition-delay: 0.2s; }

	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		height: $line-h;
		width: $line-w;
		margin: -#{round($line-h / 2)} 0 0 -#{round($line-w / 2)};
		top: 50%;
		left: 50%;
		background-color: currentColor;
		// transition
		transition: background-color $animation-speed ease-in-out; }

	&::before {
		transform: rotate(0.125turn); }

	&::after {
		transform: rotate(-0.125turn); }

	// hover
	.no-touchevents &:hover {
		&::before, &::after {
			background-color: $base-color; } } }

/* pop end */

/* table-resp */
.table-resp {
	margin-bottom: $bottom-space--article;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--article--mob; }

	&:last-child {
		margin-bottom: 0; }

	table, tbody, tr, td, th {
		@include media('<tab-lg') {
			display: block; } }

	thead {
		@include media('<tab-lg') {
			display: none; } }

	tbody {
		@include media('<tab-lg') {
			@include clear-spacing;
			@include margin-horizontal(-#{round($offers-slider-space / 2)}); } }

	table {
		border: none;
		margin: 0;
		width: 100%; }

	tr {
		@include media('<tab-lg') {
			display: inline-block;
			vertical-align: top;
			padding: 1.6rem nth($grid-space--mob, 2) 1.6rem;
			background-color: $base-color-alt;
			width: calc(50% - #{$offers-slider-space});
			margin: 0 round(round($offers-slider-space / 2)) 19px; }

		@include media('<mob-lg') {
			width: calc(100% - #{$offers-slider-space}); }

		&.closed {
			td, th {
				opacity: 0.4; }

			.td-schedule {
				opacity: 1;

				.pop__open {
					opacity: 0.4;

					@include media('<tab-lg') {
						opacity: 1; } } } } }

	td, th {
		color: $text-color;
		border: 0 solid rgba(white, 0.2);
		@include font-normal;
		@include font(2.2rem, 2.6rem);
		letter-spacing: $letter-spacing;
		text-align: left;
		vertical-align: top;
		@include padding-vertical(0.8rem);
		padding-left: 2rem;

