		margin-bottom: 0; } }

.offers__list {
	overflow: hidden;

	// only for mobile
	@include media('<mob-lg') {
		// before slide load
		white-space: nowrap;
		overflow: visible;
		opacity: 0;

		&.slick-initialized, .no-js & {
			// after slide load
			white-space: normal;
			opacity: 1; }

		.slick-list {
			overflow: visible;
			@include margin-horizontal(-#{round($offers-slider-space / 2)}); } } // компенсировать отступы между слайдами

	// destroy slider for desktop
	@include media('>=mob-lg') {

		&.slick-slider {
			-webkit-user-select: auto;
			-moz-user-select: auto;
			-ms-user-select: auto;
			user-select: auto;
			touch-action: auto;
			-ms-touch-action: auto; }

		.slick-track {
				opacity: 1 !important;
				width: auto !important;
				transform: none !important; }

		.slick-slide {
			width: 100% !important;

			&.slick-cloned {
				display: none !important; } }

		.slick-dots {
			display: none !important; } }

	&, &.slick-dotted {
		margin: 0; }

	//&.slick-dotted
	//include media('<mob-lg')
 }	//margin-bottom: $bottom-space--element

.offers__heads, .offers__item .tr {
	@include clear-spacing; }

.offers__heads, .offers__item {
	.td {
