		vertical-align: top;
		white-space: normal;
		width: $item-width;

		@include media('<mob-lg') {
			width: round-float(100% / 2, 2); }

		&:first-child {
			position: relative;
			width: round-float(100% - ($item-width * 4), 2); } } }

.offers__heads {
	@include padding-vertical(0.8rem, 1.5rem);

	@include media('<mob-lg') {
