	.td {
		@include font(1.6rem, 2.4rem);
		letter-spacing: $letter-spacing;
		color: rgba($text-color, 0.4); } }

// slide
.offers__item {
	$space-v: 1.8rem 2rem;
	//================
	position: relative;
	border-top: 1px solid rgba($text-color, 0.2);
	color: inherit;
	margin-top: -1px;

	// only for mobile
	@include media('<mob-lg') {
		border: none;
		margin: 0;
		@include padding-horizontal(round($offers-slider-space / 2)); // добавить отступы между слайдами
		// before slide load
		float: none;
		display: inline-block;
		vertical-align: top;
		width: 100%;

		.slick-initialized & {
			// after slide load
			float: left;
			display: block; } }

	.td {
		@include font(2.2rem, 3.0rem);
		letter-spacing: $letter-spacing;
		color: inherit;

		@include media('<mob-lg') {
			display: inline-block;
			vertical-align: top;
			@include font-medium;
			@include font(2.0rem, 2.4rem);
			padding-top: 1.1rem;
			width: round-float(100% / 2, 2);
			box-shadow: none; }

		.offers_main & {
			@include font-bold;
			@include font(2.6rem, 2.6rem);

			@include media('<mob-lg') {
				@include font-medium;
				font-size: 2.0rem;
				@include font(2.0rem, 2.4rem); }

			@include media('<mob-lg'); }

		&::before {
			display: none;
			content: attr(data-label);
			margin-bottom: 1px;
			@include font-normal;
			@include font(12px, 15px);
			color: $text-color-light;

			@include media('<mob-lg') {
				display: block; } }

		&:nth-child(even) {
			@include media('<mob-lg') {
