				padding-right: 0; } }

		&.offers__item__caption {
			@include media('<mob-lg') {
				display: block;
				width: auto;
				background-color: #fff;
				padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 1) * 0.8, 2);
				text-align: center;
				margin: -#{nth($grid-space--mob, 1)} -#{nth($grid-space--mob, 2)} 0.5rem;

				&::before {
					display: none; } }

			//&:hover
			//offers__item__title
 } }			//color: $highlight-color

	.tr {
		@include padding-vertical(nth($space-v, 1), nth($space-v, 2));
		// transition
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed;
			timing-function: ease-in-out; }

		@include media('<mob-lg') {
			padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 1) * 0.8, 2);
			background-color: $base-color-alt; }

		.offers_main & {
