	&:first-child, &.slick-cloned + & {
		@include media('>=mob-lg') {
			.tr {
				padding-top: 0; }

			// если у "таблицы" есть шапка
			.offers__heads + .offers__list & {
				.tr {
					padding-top: nth($space-v, 1); } } } } }

.offers__item__img {
