		margin: 0;
		border: none;
		box-shadow: none;
		background-color: $highlight-color;
		border-radius: round($form-input-height / 2);
		color: $form-bg-color;
		// transition
		$prop: color, background-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed;
			timing-function: ease-in-out; }

		@include media($form-media--mob) {
			@include size($form-input-height--mob);
			border-radius: round($form-input-height--mob / 2); }

		&:hover {
			background-color: $highlight-color--hover; }

		svg {
			$size: 24px;
			display: block;
			position: absolute;
			left: 50%;
			top: 50%;
			@include size($size);
			margin: -#{round($size / 2)} 0 0 -#{round($size / 2)};
			// transition
			$prop: opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $animation-speed;
				timing-function: ease-in-out; }

			path {
				fill: currentColor; } } }

	// Form error
	.error ~ button {
		background-color: $form-error-color; }

	// Form complete
	.form-success {
		.success-note {
			display: block; }

		.input-holder {
			&.success {
				&::before, &::after {
					opacity: 1; } } }

		input[type="email"], input[type="input"] {
			&:not(:disabled) {
				background-color: #56a4a6;
				border-color: #56a4a6; } }

		button {
			svg {
				opacity: 0; } } } }

