/* big button of contacts */
$big-btn-size: 876px 880px;
$big-btn-size--max: 956px 960px;
$scale: round-float(nth($big-btn-size--max, 2) / nth($big-btn-size, 2), 2);
.big-btn {
	display: block;
	overflow: hidden;
	position: absolute;
	right: 0;
	bottom: 0;
	@include size(round(nth($big-btn-size--max, 1) / 2), round(nth($big-btn-size--max, 2) / 2));
	text-align: left;
	@include font-bold;
	@include font(2.6rem, round-float(32/26, 2));
	color: white;
	z-index: -1;

	@include media('<tab-lg') {
		display: none; }

	& > span {
		display: block;
		position: absolute;
		width: 330px;
		right: 5rem;
		bottom: 3.2rem; }

	svg {
		display: block;
		position: absolute;
		right: 5.5rem;
		bottom: 2.8rem;
		@include size(30px);
		margin-bottom: 0.3rem;
		animation-iteration-count: infinite;
		animation-duration: 1.5s;
		animation-fill-mode: both;
		//$prop: transform
		//will-change: $prop
		//transition:
		//roperty: $prop
		//uration: $animation-speed
		//iming-function: ease-in-out

		path {
			fill: currentColor; } }

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 100%;
		top: 100%;
		@include size(nth($big-btn-size, 1), nth($big-btn-size, 2));
		margin: -#{round(nth($big-btn-size, 1) / 2)} 0 0 -#{round(nth($big-btn-size, 2) / 2)};
		background-color: $highlight-color;
		border-radius: 50%;
		animation-iteration-count: infinite;
		animation-duration: 1.5s;
		animation-fill-mode: both;
		// transition
		$prop: background-color, transform;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed;
			timing-function: ease-in-out; } }

	// hover
	.no-touchevents &:hover {
		color: white;

		svg {
			//$tX: 1rem
			//transform: translate($tX, 0)
			//transform: translateX($tX)
			animation-name: pulseArr; }

		&::before {
			background-color: $highlight-color--hover;
			animation-name: pulse; } } }

@keyframes pulse {

	0% {
		$scale: 1;
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1); }

	33% {
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1); }

	to {
		$scale: 1;
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1); } }

@keyframes pulseArr {

	0% {
		$tX: 0;
		transform: translate($tX, 0);
		transform: translateX($tX); }

	33% {
		$tX: 1rem;
		transform: translate($tX, 0);
		transform: translateX($tX); }

	to {
		$tX: 0;
		transform: translate($tX, 0);
		transform: translateX($tX); } }

/* big button of contacts end */

