/* rolls */
.rolls {
	margin: 0 auto $bottom-space--element;
	max-width: $text-layout-width + nth($page-layout-space, 2) * 2;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob;
		//max-width: $text-layout-width + nth($page-layout-space--mob, 2) * 2
		max-width: none; }

	&:last-child {
		margin-bottom: 0; } }

// item
.rolls__item {
	position: relative;
	margin-bottom: nth($grid-space, 2);
	border: 1px solid $base-color-alt;

	@include media('<tab-lg') {
		margin-bottom: nth($grid-space--mob, 2); }

	@include media('<mob-lg') {
		padding-bottom: 3.9rem; }

	&:last-child {
		margin-bottom: 0; } }

// header
.rolls__header {
	position: relative;
	color: inherit;
	background-color: $base-color-alt;
	padding: 3rem nth($page-layout-space, 2) 3.3rem;
	// transition
	$prop: background-color;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	.touchevents & {
		outline: none;
		-webkit-tap-highlight-color: transparent; }

	@include media('<tab-lg') {
		padding: 1.5rem 6rem 1.5rem nth($page-layout-space--mob, 2); }

	@include media('<mob-lg') {
		position: static;
		padding: 1.1rem nth($page-layout-space--mob, 2) 1.5rem; }

	// hover
	.no-touchevents &:not(.is-open):hover {
		background-color: $base-color-dark; } }

.rolls__header__inner {
	text-align: left;
	color: inherit;
	margin: 0 auto;
	max-width: $text-layout-width;
	cursor: default;
	line-height: 3.0rem;

	@include media('<mob-lg') {
		@include font(2.0rem, 2.4rem); } }

// angle
.rolls__angle {
	$burger-size: nth($rolls-angle-size, 1) nth($rolls-angle-size, 2);
	$burger-size--md: nth($rolls-angle-size--md, 1) nth($rolls-angle-size--md, 2);
	$burger-size--mob: nth($rolls-angle-size--mob, 1) nth($rolls-angle-size--mob, 2);
	$layer-size: 2px 20px;
	$layer-space: 4px;
	$layer-color: white;
	$burger-animation-speed: $animation-speed;
	$burger-animation-delay: round-float($burger-animation-speed*0.5, 2);

	$cross-size: nth($layer-size, 1) nth($layer-size, 2);
	$cross-color: $layer-color;

	// =====================

	position: absolute;
	right: 0;
	bottom: 0;
	@include size(nth($burger-size, 1), nth($burger-size, 2));
	background-color: $highlight-color;
	//box-shadow: inset 0 0 0 1px $highlight-color--hover
	box-shadow: inset 0 0 0 $border-color;
	cursor: pointer;
	outline: none;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	// transition
	$prop: background-color, box-shadow;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	@include media('<tab-lg') {
		@include size(nth($burger-size--md, 1), nth($burger-size--md, 2)); }

	@include media('<mob-lg') {
		@include size(nth($burger-size--mob, 1), nth($burger-size--mob, 2)); }

	// крест
	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(nth($cross-size, 1), nth($cross-size, 2));
		margin-top: -#{round(nth($cross-size, 1) / 2)};
		margin-left: -#{round(nth($cross-size, 2) / 2)};
		background-color: $cross-color;
		opacity: 0;
		// transition
		$prop: transform, opacity, background-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $burger-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	// бутерброд
	& > i {
		//средний слой бутерброда
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(#{nth($layer-size, 1)}, #{nth($layer-size, 2)});
		margin-top: -#{round(nth($layer-size, 1) / 2)};
		margin-left: -#{round(nth($layer-size, 2) / 2)};
		background-color: $layer-color;
		// transition
		$prop: transform, opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $burger-animation-speed;
			delay: $burger-animation-delay; }

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include size(#{nth($layer-size, 1)}, #{nth($layer-size, 2)});
			background-color: $layer-color;
			// transition
			$prop: transform;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $burger-animation-speed;
				delay: $burger-animation-delay; } }

		//верхний слой бутерброда
		&::before {
			top: -#{nth($layer-size, 1) + $layer-space}; }

		//нижний слой бутерброда
		&::after {
			top: #{nth($layer-size, 1) + $layer-space}; } }

	// hover
	.no-touchevents &:hover {
		background-color: $highlight-color--hover; }

	// active
	.is-open & {
		&::before, &::after {
			transition-delay: $burger-animation-delay;
			//background-color: $base-color

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{nth($layer-size, 1) + $layer-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{nth($layer-size, 1) + $layer-space}) translateZ(0); } } } }

// panel
.rolls__panel {
	display: none; // Важно!
	position: relative;
	padding: nth($page-layout-space, 1) nth($page-layout-space, 2);
	background-color: $base-color-dark;

	@include media('<tab-lg') {
		padding: nth($page-layout-space--mob, 1) 6rem nth($page-layout-space--mob, 1) nth($page-layout-space--mob, 2); }

	@include media('<mob-lg') {
		padding: 1.4rem nth($page-layout-space--mob, 2) 1.6rem;
		line-height: 1.9rem; }

	&::before {
		content: "";
		display: block;
		position: absolute;
		right: 0;
		bottom: 0;
		width: 6rem;
		height: 100%;
		background-color: #488098;

		@include media('<tab-lg') {
			width: 4rem; }

		@include media('<mob-lg') {
			display: none; } }

	// active
	&.is-open {
		display: block; } // Важно!

	h3, h4 {
		color: $highlight-color; } }

.rolls__panel__inner {
	margin: 0 auto;
	max-width: $text-layout-width;
	opacity: 0;
	pointer-events: none;
	will-change: opacity;
	transition: opacity $animation-speed * 2 ease-in-out;

	// active
	.is-open & {
		opacity: 1;
		pointer-events: auto; } }

/* rolls end */

/* contacts */
.contacts {
	$space-b: $cont-map-height - nth($main-space-v, 2); //Место для карты. С учетом отступов
	position: relative;
	min-height: 39rem + $space-b;
	padding-bottom: $space-b;
	margin-top: -5rem;

	@include media('<tab-lg') {
		min-height: 0;
		padding-bottom: 0; }

	@include media('<mob-lg') {
		margin-top: 0; } }

.contacts__holder {
	position: relative;
	z-index: 5;

	&::before {
		content: '';
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		width: $cont-form-width;
		height: calc(100% + 6rem);
		background-color: $base-color-alt;

		@include media('<tab-lg') {
			display: none; } } }

.cont {
	display: inline-block;
	vertical-align: top;
	width: 100%;

	& > ul {
		list-style: none;
		padding: 0;
		margin: -5px 0 0;

		@include media('<desk-sm') {
			margin-top: 0; }

		li {
			padding: 0;
			@include font(2.2rem, 3.0rem);
			margin-bottom: 1.4rem;

			@include media('<mob-lg') {
				@include font(1.6rem, 1.9rem);

				margin-bottom: 0.7rem; }

			&::before {
				content: none; } }

		& > li {
			padding-left: 4.2rem;

			//+media('<tab-lg')
			//loat: left
 }			//idth: round-float($hundred / 2, 2)

		a {
			color: currentColor;

			//.no-touchevents &:not([href^="tel:"]):hover // если не нужнен ховер на телефоны
			.no-touchevents &:hover {
				color: $highlight-color; }

			&[href^="tel:"] {
				display: inline-block;
				vertical-align: top;
				@include font-bold;
				font-size: 2.4rem;
				white-space: nowrap;

				@include media('<mob-lg') {
					@include font-medium;
					@include font($font-size--mob, $line-height--mob); }

				& > span {
					@include font-light;
					font-size: 0.8em; } } }

		ul {

			& > li {
				margin-bottom: 0; } }

		svg {
			display: block;
			position: absolute;
			left: 0;
			top: 3px;
			@include size(2.4rem); }

		path {
			fill: $highlight-color; } } }

.contact-share, .contacts-adr {
	margin-right: $cont-form-width;

	@include media('<tab-lg','>=tab-sm') {
		margin-right: $cont-form-width--sm; }

	@include media('<tab-sm') {
		margin-right: 0; } }

.contact-share {
	min-height: 23.4rem;
	padding-top: 6rem;
	padding-bottom: 5.7rem;
	@include clear;

	@include media('<desk-sm') {
		padding-top: 0; }

	@include media('<tab-lg') {
		min-height: 0;
		padding-bottom: $bottom-space--article--mob; }

	@include media('<tab-sm') {
		padding-bottom: 2rem; }

	.contact-share__box {
		float: left;
		padding-right: 2rem;

		@include media('<desk-sm') { // 1280
			float: none;
			padding-right: 0;
			padding-bottom: 2rem;
			width: auto; }

		&:first-child {
			width: 49%;

			@include media('<desk-sm') {
				width: auto; }

			//+media('<tab-lg')
			//loat: left
 }			//idth: #{round-float($hundred / 3, 2)}

		&:last-child {
			padding-bottom: 0;
			width: 51%;

			@include media('<desk-sm') {
				width: auto; }

			//+media('<tab-lg')
			//loat: left
 } }			//idth: #{round-float($hundred / 3 * 2, 2)}

	//.social-networks
	//include media('<tab-lg')
 }	//display: none

.contacts-adr {
	display: block;
	padding: 5rem 6rem 5.2rem;
	font-style: normal;
	@include font(1.6rem, 2.4rem);
	color: rgba($base-color-alt, 0.7);
	background-color: white;

	@include media('<desk-sm') {
		padding: 4rem 6rem 4.2rem; }

	@include media('<tab-lg') {
		padding: 2rem 3rem; }

	@include media('<tab-sm') {
		padding: $page-layout-space--mob;
		@include margin-horizontal(-#{nth($page-layout-space--mob, 2)}); }

	& > strong {
		display: block;
		color: $base-color;

		& {
			@include font-h4(); } } }

.contacts-map {
	$space-h: -#{nth($page-layout-space, 2)};
	//position: relative
	position: absolute;
	left: $space-h;
	right: $space-h;
	bottom: 0;
	//+margin-horizontal($space-h)
	margin-bottom: -#{nth($main-space-v, 2)};

	@include media('<desk-lg') {
		$space-h: -#{nth($page-layout-space--md, 2)};
		left: $space-h;
		right: $space-h; }

	@include media('<tab-lg') {
		position: relative;
		left: auto;
		right: auto;
		@include margin-horizontal(-#{nth($page-layout-space--mob, 2)});
		margin-bottom: 0; }

	.map-box {
		height: $cont-map-height;

		@include media('<tab-lg') {
			height: 320px; } } }

.contacts-form {
	position: absolute;
	width: 100%;
	left: 0;
	top: 0;
	height: 0;
	z-index: 10;

	@include media('<tab-sm') {
		position: relative;
		left: auto;
		top: auto;
		height: auto; } }

.contacts-form__holder {
	position: relative;
	height: 100%; }

.contacts-form {
	.user-form {
		position: absolute;
		top: 0;
		right: 0;
		width: $cont-form-width;
		padding: 4.9rem 6rem 6rem;
		border-radius: 0;
		background-color: $base-color-alt;

		@include media('<tab-lg','>=tab-sm') {
			width: $cont-form-width--sm;
			padding: 2rem 3rem;
			margin: 0; }

		@include media('<tab-sm') {
			position: relative;
			top: auto;
			right: auto;
			width: auto;
			padding: nth($page-layout-space--mob, 1) nth($page-layout-space--mob, 2);
			margin-bottom: -#{nth($main-space-v--md, 2)}; }

		@include media('<mob-lg') {
			margin-bottom: -#{nth($main-space-v--mob, 2)}; }

		form {
			@include media('<tab-lg') {
				padding: 0; } }

		.form-row {
			margin: 0;

			.input-wrap {
				display: block;
				width: auto;
				@include margin-horizontal(0);
				margin-bottom: 2.2rem;

				&:last-child {
					margin-bottom: 0; } } }

		.form-title {
			text-align: left;
			margin-bottom: 2.1rem;

			@include media('<tab-lg') {
				@include padding-horizontal(0); }

			@include media('<tab-sm') {
				margin-bottom: 0.7rem; } }

		.form-buttons {
			padding-top: 1.8rem;

			@include media('<mob-lg') {
				padding-top: 0; }

			label, .label-holder {
				display: none; }

			button {
				max-width: none; } } }

	.user-form__msg {
		border-radius: 0;
		padding: 3rem; }

	.user-form__msg__caption {
		margin-bottom: 3rem; }

	.user-form__msg__title {
		@include font(5.2rem, 5.2rem);

		@include media('<tab-lg') {
			@include font(3rem, 3.2rem); } }

	.user-form__msg__subtitle {
		@include font(2.6rem, 2.6rem);

		@include media('<tab-lg') {
			@include font(2rem, 2.8rem); } }

	.user-form__msg__tel {
		margin-bottom: 3rem;
		@include font(3.2rem, 3.6rem);

		@include media('<tab-lg') {
			margin-bottom: 0;
			@include font(2.6rem, 3.2rem); } } }

