@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* === LAYOUT === */
html {
	font-size: 62.5%;
	//+scroll-blocked

	// блокировка скролла
	&.css-scroll-fixed:not(.open-only-mob) {
		@include scroll-blocked; }

	&.css-scroll-fixed.open-only-mob {
		@include media('<desk-lg') {
			@include scroll-blocked; } } }

.max-wrap {
	max-width: $page-layout-width; }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto;

	@include media('<desk-lg') {
		@include padding-horizontal(nth($page-layout-space--md, 2)); }

	@include media('<tab-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

.layout-space {
	@include padding-horizontal(nth($page-layout-space, 2));

	@include media('<desk-lg') {
		@include padding-horizontal(nth($page-layout-space--md, 2)); }

	@include media('<tab-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
//.layout-article
.#{$user-content} {
	& > p, & > ul, & > ol,
	& > h2, & > h3, & > h4, & > h5, & > h6,
	& > .h2, & > .h3, & > .h4, & > .h5, & > .h6,
	& > hr,
	& > .layout-text {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto;

		@include media('<mob-lg') {
			max-width: none; }

		.home-page & {
			@include wide; } }

	& > h1, & > .h1 {
		max-width: none; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto;

		@include media('<tab-lg') {
			max-width: none; } }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	//.layout-full-width, .layout-wide

	//include margin-horizontal(-#{nth($text-layout-space, 2)})

	//include media('<tab-lg')
	//@include margin-horizontal(-#{nth($text-layout-space--md, 2)})

	//include media('<tab-sm')
 }	//@include margin-horizontal(-#{nth($text-layout-space--sm, 2)})

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	//max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $text-layout-width;

	@include media('<tab-lg') {
		max-width: none; }

	//+media('<tab-sm')
 }	//ax-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	//max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $text-layout-width;
	//+padding-horizontal(nth($text-layout-space, 2))
	@include push-auto;

	//+media('<tab-lg')
	//ax-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2}
	//include padding-horizontal(nth($text-layout-space--md, 2))

	//+media('<tab-sm')
	//ax-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}
 }	//include padding-horizontal(nth($text-layout-space--sm, 2))

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	//max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $flood-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$flood-layout-width + (nth($text-layout-space--mob, 2))*2}

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	//max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $flood-layout-width;
	//+padding-horizontal((nth($text-layout-space, 2)))
	@include push-auto;

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}
	//include padding-horizontal((nth($text-layout-space--md, 2)))

	//+media('<tab-sm')
	//ax-width: #{$flood-layout-width + (nth($text-layout-space--sm, 2))*2}
	//include padding-horizontal((nth($text-layout-space--sm, 2)))

	p, ul, ol, h2, h3, h4, h5, h6, hr,
	.h2, .h3, .h4, .h5, .h6 {
		max-width: none; } }

// елементы с максимальной ограниченной шириной
.max-wrap-wide {
	max-width: $wide-layout-width; }

.layout-wide {
	max-width: $wide-layout-width;
	@include push-auto; }

// елементы с максимальной ограниченной шириной без отступов
.max-wrap-wide-full {
	max-width: $wide-full-layout-width; }

.layout-wide-full {
	max-width: $wide-full-layout-width;
	@include push-auto; }

// выравнивание по центру блочных элементов
.push-auto {
	margin-left: auto;
	margin-right: auto; }

// layout страницы
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	//min-height: 100%
	//height: 100%
	min-height: 100vh;
	max-width: 100%;
	width: $page-layout-width; }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	position: relative;
	z-index: $z-middle;
	padding-bottom: nth($main-space-v, 2);

	@include media('<desk-lg') {
		z-index: $z-middle; }

	@include media('<tab-lg') {
		padding-bottom: nth($main-space-v--md, 2); }

	@include media('<mob-lg') {
		padding-bottom: nth($main-space-v--mob, 2); }

	&.home-page {
		padding-bottom: 13.1rem;

		@include media('<tab-lg') {
			padding-bottom: nth($main-space-v--md, 2); }

		@include media('<mob-lg') {
			padding-bottom: nth($main-space-v--mob, 2); } } }

.content {
	//overflow: hidden // Нельзя использовать overflow: hidden. На странице контактов обрезает карту
	min-height: 5rem;

	.contacts-page & {
		overflow: visible; } }

/* === CONTENT end === */

/* === HEADER === */
.header {
	position: fixed;
	left: 0;
	top: 0;
	height: 0;
	width: 100%;
	z-index: $z-low;

	@include media('<desk-lg') {
		z-index: $z-high; } }

.header__layout {
	position: relative;
	margin: 0 auto; }

.header__holder {
	position: absolute;
	right: 0;
	top: 0;
	height: $header-height;
	width: $header-width;
	padding: nth($page-layout-space, 1) 60px;
	z-index: $z-low;
	background-color: $header-bg;
	@include clear;

	@include media('<desk-lg') {
		right: auto;
		left: 0;
		height: 0;
		width: $header-width--md;
		padding: 0;
		z-index: $z-middle; } }

//header top
.header__top {
	position: relative;
	//margin-bottom: 10.6rem
	margin-bottom: 7rem;

	@include media('<desk-lg') {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: $header-height--md;
		margin: 0;
		padding: 1rem nth($page-layout-space--md, 2);
		background-color: $header-bg;
		z-index: $z-middle; }

	@include media('<tab-lg') {
		padding: 1rem nth($page-layout-space--mob, 2);
		height: $header-height--mob; }

	&:last-child {
		margin-bottom: 0; } }

//logo
.logo {
	display: block;
	position: relative;
	background-image: url(../../img/logo.png);
	background-repeat: no-repeat;
	background-size: contain;
	width: 155px;
	color: #cccddb;
	z-index: 5;

	@media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi) {
		background-image: url(../../img/logo-2x.png); }

	@include media('<desk-lg') {
		background-image: url(../../img/logo-sm-2x.png);
		width: 165px;
		margin-top: -13px; }

	@include media('<tab-lg') {
		width: 93px;
		margin-top: 0;
		color: #a7a7ce; }

	&::before {
		content: "";
		display: block;
		@include ratio(155, 33); }

	a, & > span {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		color: inherit;
		text-decoration: none;
		@include size(100%);

		& > em {
			display: block;
			width: 200px;
			position: absolute;
			left: 100%;
			top: 0;
			padding-left: 36px;
			margin-top: -1px;
			@include font-normal;
			@include font(16px, 19px);

			@include media('<desk-lg') {
				padding-left: 41px;
				margin-top: 4px; }

			@include media('<tab-lg') {
				width: 110px;
				padding-left: 14px;
				margin-top: -1px;
				@include font(10px, 12px); } } }

	& > span {
		cursor: default; } }

//header-menu
.header-menu {
	display: none;
	$hs: 6rem;
	list-style: none;
	padding: 0;
	//margin: 0 -#{round($hs / 2)}
	margin: 0;
	@include font-bold;
	//+font(3.6rem, $header-height--md)
	@include font(3.6rem, 4.0rem);
	@include clear;

	@include media('<desk-lg') {
		display: block;
		padding-right: $header-height--md; }

	@include media('<tab-lg') {
		display: none; }

	li {
		float: left;
		//+padding-horizontal(round($hs / 2))
		padding-left: $hs;

		&.small {
			position: relative;
			bottom: -5px;
			font-size: 2.0rem;
			@include font-light;

			@include media('<1200px') {
				display: none;

				& + li {
					padding-left: 0; } } }

		&:first-child {
			padding-left: 0; } }

	a {
		color: $base-color;
		transition: color $animation-speed ease-in-out;

		.no-touchevents &:not([href*="javascript:;"]):hover {
			color: $highlight-color; } }

	// current
	li.current {
		a {
			color: $highlight-color; } } }

