// preparation heading
.c-heading {
	$tf: cubic-bezier(0.4, 0.6, 0.2, 1);
	position: fixed;
	height: 0;
	top: 0;
	left: 0;
	width: 100%;
	z-index: $z-heading;
	pointer-events: none;
	opacity: 0;
	transition: {
		property: opacity, animation;
		duration: 0.8s;
		timing-function: $tf; }

	animation: {
		duration: 1s;
		timing-function: $tf; }

	&.is-active {
		opacity: 1;
		transition: {
			delay: 0.2s; }
		animation: {
			fill-mode: forwards;
			name: showHeading; } }

	&.is-leaving {
		opacity: 0;
		animation: {
			fill-mode: forwards;
			duration: 0s;
			delay: 1s;
			name: hideHeading; }

		transition: {
			timing-function: ease-in-out; } }

	figure {
		display: none;
		opacity: 0;
		pointer-events: none;
		visibility: hidden; }

	&.has-logo figure {
		display: block; } }

.c-heading__layout {
	position: relative; }

.main,
.c-heading__align {
	margin-right: $header-width;
	width: calc(100% - #{$header-width});

	@include media('<desk-lg') {
		margin-right: 0;
		width: 100%;
		padding-top: $header-height--md; }

	@include media('<tab-lg') {
		padding-top: $header-height--mob; } }

$c-heading-y: 60vh;
@keyframes showHeading {
	0% {
		transform: translate(0, $c-heading-y); }

	30% {
		transform: translate(0, $c-heading-y); }

	100% {
		transform: translate(0, 0); } }

@keyframes hideHeading {
	0% {
		transform: translate(0, 0); }

	100% {
		transform: translate(0, $c-heading-y); } }

